<template>
    <div id="customPanel">
        <!-- To put transparent: bgOverlay="rgb(0,0,0,0.8)" -->
        <vue-modaltor :bgPanel="'#00222E'" bgOverlay="rgb(0,0,0,0.8)" :resize-width='{1200:"80%",992:"80%",768:"93%"}'
                      :visible="this.open"
                      @hide="hideModal">
            <template slot="close-icon">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 40 40" width="20" height="20"
                     xml:space="preserve">
            <path class="st0" fill="white"
                  d="M8.7,7.6c-0.4-0.4-1-0.4-1.4,0C6.9,8,6.9,8.6,7.3,9l11,11l-11,11c-0.4,0.4-0.4,1,0,1.4c0.4,0.4,1,0.4,1.4,0 l11-11l11,11c0.4,0.4,1,0.4,1.4,0c0.4-0.4,0.4-1,0-1.4l-11-11L32,9c0.4-0.4,0.4-1,0-1.4c-0.4-0.4-1-0.4-1.4,0l-11,11L8.7,7.6z"/>
        </svg>
            </template>
            <div v-if="this.open">

                <PanelCountry :idJson="this.idJson"></PanelCountry>
            </div>
        </vue-modaltor>

    </div>
</template>

<script>
    import {EventBus} from './../event-bus.js';
    import PanelCountry from "./PanelCountry";

    export default {
        name: "CustomPanel",
        components: {
            PanelCountry,
        },

        data() {
            return {
                open: false,
                idJson: 'none'
            }
        },

        mounted() {
            EventBus.$on('clickPanel', (idJson) => {
                this.open = true;
                this.idJson = idJson;
            })
        },

        methods: {
            hideModal() {
                this.open = false;
            }
        },
    }
</script>

<style scoped>
    #customPanel {
        background-color: #00222E;
    }
</style>